
.modal-mask{position:fixed;z-index:9998;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);display:table
}
.modal-wrapper{display:table-cell;vertical-align:middle
}
.modal-container{width:800px;margin:0px auto;padding:20px 30px;background-color:#fff;border-radius:2px;box-shadow:0 4px 16px rgba(0,0,0,0.33);transition:all .3s ease;font-family:Helvetica, Arial, sans-serif
}
.modal-header{margin-top:0;color:#42b983
}
.modal-body{margin:20px 0
}



